import React from 'react';
import { Link } from 'react-router-dom';

const home = () => (
    <div className="container">
        <div className="jumbotron mt-5">
            <h1 className="display-4">Welcome to my blog about life.</h1>
            <p className="lead">We make all kind of awesome blogs about life event and lifestyles.</p>
            <hr className="my-4" />
            <p>Click on the button below to check out our awesome blogs.</p>
            <Link className="btn btn-primary btn-lg" to="/blog" role="button">Check out our blogs</Link>
        </div>
    </div>
    
);

export default home;